import React, { useRef } from "react";

import {
	Modal,
	ModalBody,
	ModalFooter,
	ModalHeader,
	ModalHeading,
} from "../Modal";
import { colors } from "../styleguide";
import { Typography } from "../Typography";

import {
	errorBoundaryPrimaryButtonText,
	errorBoundarySecondaryButtonText,
	errorBoundaryText,
	errorBoundaryTitle,
} from "./ErrorBoundaryComponent.constants";
import { ErrorBoundaryProps } from "./ErrorBoundaryComponent.types";

const ErrorBoundaryComponent = ({
	dataQA,
	className,
	modalTitle = errorBoundaryTitle,
	modalBodyText = errorBoundaryText,
	modalPrimaryButtonText = errorBoundaryPrimaryButtonText,
	modalSecondaryButtonText = errorBoundarySecondaryButtonText,
	modalPrimaryAction,
	modalSecondaryAction,
}: ErrorBoundaryProps) => {
	const errorBoundaryRef = useRef<HTMLDivElement>(null);

	const tryAgainHandler = () => {
		window.location.reload();
	};

	const visitHelpCenterHandler = () => {
		window.open("https://support.wunderkind.co/hc/en-us", "_blank", "noopener noreferrer");
	};

	return (
		<div ref={errorBoundaryRef} className={className}>
			<Modal
				appElement={errorBoundaryRef.current || document.body}
				contentLabel="Error boundary modal"
				dataQA={dataQA}
				onRequestClose={function noRefCheck() {}}
				preventBackgroundScroll
				size="small"
				variant="destructive"
				isOpen
			>
				<ModalHeader dataQA="error-boundary-modal-header" showCloseButton={false}>
					<ModalHeading
						headingIcon="TriangleAlert"
						component="h1"
						dataQA="error-boundary-modal-heading"
					>
						{modalTitle}
					</ModalHeading>
				</ModalHeader>
				<ModalBody dataQA="playground-modal-body">
					<Typography
						component="p"
						dataQA="first-paragraph"
						variant="bodyCopy"
						color={`${colors.black}A6`}
					>
						{modalBodyText}
					</Typography>
				</ModalBody>
				<ModalFooter
					dataQA="error-boundary-modal-footer"
					primaryButtonText={modalPrimaryButtonText}
					primaryButtonOnClick={modalPrimaryAction || tryAgainHandler}
					secondaryButtonText={modalSecondaryButtonText}
					secondaryButtonOnClick={modalSecondaryAction || visitHelpCenterHandler}
				/>
			</Modal>
		</div>
	);
};

ErrorBoundaryComponent.displayName = "ErrorBoundary";

export default ErrorBoundaryComponent;

